import { userConstants } from '../_constants';
import { userService } from '../_services';
import { alertActions } from './';
import { history } from '../_helpers';

export const userActions = {
    buyBtc,
    sellBtc,
    login,
    logout,
    register,
    getAll,
    delete: _delete
};

function login(username, password) {
    return dispatch => {
        dispatch(request({ username }));

        userService.login(username, password)
            .then(
                ({user, rates}) => {
                    dispatch(success(user, rates));
                    history.push('/');
                },
                error => {
                    dispatch(failure(error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };

    function request(user) { return { type: userConstants.LOGIN_REQUEST, user } }
    function success(user, rates) { return { type: userConstants.LOGIN_SUCCESS, user, rates } }
    function failure(error) { return { type: userConstants.LOGIN_FAILURE, error } }
}

function logout() {
    userService.logout();
    return { type: userConstants.LOGOUT };
}

function register(user) {
    return dispatch => {
        dispatch(request(user));

        userService.register(user)
            .then(
                user => {
                    dispatch(success());
                    history.push('/login');
                    dispatch(alertActions.success('Registration successful, you may now log in.'));
                },
                error => {
                    dispatch(failure(error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };

    function request(user) { return { type: userConstants.REGISTER_REQUEST, user } }
    function success(user) { return { type: userConstants.REGISTER_SUCCESS, user } }
    function failure(error) { return { type: userConstants.REGISTER_FAILURE, error } }
}

function getAll() {
    return dispatch => {
        dispatch(request());

        userService.getAll()
            .then(
                users => dispatch(success(users)),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: userConstants.GETALL_REQUEST } }
    function success(users) { return { type: userConstants.GETALL_SUCCESS, users } }
    function failure(error) { return { type: userConstants.GETALL_FAILURE, error } }
}

function buyBtc(pair, amount, id) {
    return dispatch => {
        dispatch(request(pair, amount, id));

        userService.buyBtc(pair, amount, id)
            .then(
                ({ user, rates}) => {
                    dispatch(success(user, rates));
                    dispatch(alertActions.success(`Trade successful, you bought ${amount} BTC with ${pair}.`));
                }
            )
            .catch(error => {
                    dispatch(failure(id, error.toString()));
                    dispatch(alertActions.error(error));
                }
            );
    };

    function request(id) { return { type: userConstants.BUY_REQUEST, id } }
    function success(user, rates) { return { type: userConstants.BUY_SUCCESS, user, rates } }
    function failure(id, error) { return { type: userConstants.BUY_FAILURE, id, error } }
}

function sellBtc(pair, amount, id) {
    return dispatch => {
        dispatch(request(pair, amount, id));

        userService.sellBtc(pair, amount, id)
            .then(
                ({ user, rates}) => {
                    dispatch(success(user, rates));
                    dispatch(alertActions.success(`Trade successful, you sold ${amount} BTC for ${pair}.`));
                }
            ).catch(error => {
                    dispatch(failure(id, error.toString()));
                    dispatch(alertActions.error(error));
                }
            );
    };

    function request(id) { return { type: userConstants.SELL_REQUEST, id } }
    function success(user, rates) { return { type: userConstants.SELL_SUCCESS, user, rates } }
    function failure(id, error) { return { type: userConstants.SELL_FAILURE, id, error } }
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    return dispatch => {
        dispatch(request(id));

        userService.delete(id)
            .then(
                user => dispatch(success(id)),
                error => dispatch(failure(id, error.toString()))
            );
    };

    function request(id) { return { type: userConstants.DELETE_REQUEST, id } }
    function success(id) { return { type: userConstants.DELETE_SUCCESS, id } }
    function failure(id, error) { return { type: userConstants.DELETE_FAILURE, id, error } }
}